.explanationSection{
    position: absolute;
    top:5%;
    left:50%;
    transform: translateX(-50%);
}

.explanationTextFirstLine{
    text-align: center;
    position: absolute;
    top:17%;
    left:50%;
    transform: translateX(-50%);
    font-size: 50px;
    font-weight: 600;
}

.explanationTextSecondLine{
    width: 1000px;
    text-align: center;
    position: absolute;
    top:42%;
    left:50%;
    transform: translateX(-50%);
    font-size: 60px;
    font-weight: 800;
}

.clueSection{
    position: absolute;
    top:30%;
    left:50%;
    transform: translateX(-50%);
}

.clickButton{
    position: absolute;
    bottom:9%;
    left: 30%;
    width: 96px;
    height: 42px;
    background-color: transparent;
}

.imageButton{
    background: none;
    border: none;
    padding: 0;
    cursor: pointer;

    position: absolute;
    top:80%;
    left:50%;
    transform: translateX(-50%);
}

.buttonDisabled{
    cursor: none;
}